@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&family=Roboto:wght@300;400;700&display=swap);
body{font-family:'Roboto', sans-serif;font-weight:400}.sans-serif{font-family:'Roboto' sans-serif}.mono{font-family:'Roboto Mono', sans-serif}@media only screen and (min-width: 960px){.container{width:960px}}

.banner{display:flex;align-items:center;justify-content:center;height:100vh !important;max-height:700px}.banner-content{height:100%;display:flex;flex-direction:column;justify-content:center;color:white;text-shadow:0 0 8px black;z-index:2;text-align:center}.who-am-i{margin-top:auto}.scroll-down{margin-top:auto;margin-bottom:80px}.scroll-down>Button{border-radius:50%;width:56px;height:56px;font-size:28px;background-color:#262A34;border-color:#262A34}.scroll-down>Button:hover,.scroll-down>Button:focus,.scroll-down>Button:active{background-color:darkslategrey !important;border-color:darkslategrey !important;box-shadow:none !important}.about{background-color:#FAFAFA;padding-top:80px;padding-bottom:80px}.about-row{align-items:center}.profile-photo{width:80%;max-width:400px;margin-left:auto;margin-right:auto}.external-links{display:flex;justify-content:center}.external-link{display:flex;font-size:24px;align-items:center;color:#212529}.external-link>svg{margin-right:14px}.experience{background-color:#262A34;color:white}.heading{border-bottom:4px solid aquamarine;margin-bottom:64px}.gray{color:#999}.light-gray{color:#ccc}.experience-name{display:flex;margin-bottom:14px}.experience-logo{padding-right:24px;width:110px;text-align:center;align-self:center}.project-card{border-radius:1rem;background-color:#f7f9fb;padding:20px;margin-bottom:2rem}.project-card:hover{background-color:#ecf0f4}.project-title{text-align:center;font-size:1.4rem;margin-top:1rem}

